*{
    margin: 0;
    padding: 0;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
}
ol, ul ,li {
    list-style: none;
}
a {
    text-decoration: none;
    color: #333;
}


.navbar {
    width: 100%;
    background-color: #00437b;
    position: fixed;
    top: 0;
    z-index: 2;

    .navbar-nav {

        .nav-link {
            color: #fff;
            font-weight: 600;
        
            &:hover {
                color: #afd32f;
            }
            
           .active {
                color: #afd32f;
           }

           .icon_font {
                font-size: 18px;
                padding: 0 5px;
                
           }

        }
    }
    
    .container {


        >a {
            color: #fff;
            font-weight: 900;

            &.navbar-brand {
                font-size: 22px;
                line-height: 10px;
            }
        }
    }
}

.slide {
    .carousel-inner {

        .carousel-item {

            .carousel-caption {
                margin-bottom: 50px;
            }
            
            img {
                width: 100%;
                height: 100%;
            }
        }
    }    
}

// 상품목록
.product_wrap {
    margin-top: 80px;

    >h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }

    .row {

        >div {

            margin: 0 0 70px;

            h4 {
                font-size: 0.95rem;
                line-height: 2;
                cursor: default;
            }

            span {
                cursor: default;
            }

            img {
                cursor: pointer;

                &:hover {
                    opacity: 0.8;
                }
            }

            .product_bottom {
                display: flex;
                align-items: center;
                justify-content: space-between;
                
                span {
                    font-size: 0.95rem;
                }

                .heart {
                    display: flex;
                    align-items: center;

                    img{
                        cursor: pointer;
                    }
                    span {
                        padding-left: 5px;
                        font-size: 12px;
                    }
                }
                
            }
            
        }
    }
}

.more_btn {
    text-align: center;
    margin: 0 0 20px;

    button {
        border-radius: 0px;
        font-size: 0.85rem;
        font-weight: 500;
    }
}

.view_wrap {
    margin: 80px auto;
    
    .view_left {
        position: relative;
        text-align: center;

        .view_text {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            cursor: default;

            h1 {
                font-weight: 600;
                color: #fff;
                letter-spacing: 3px;
            }

            div {
                display: inline-block;
                border: 1px solid #fff;
                padding: 10px 30px;
                color: #fff;
                margin-top: 40px;
                cursor: pointer;

                &:hover {
                    border: 1px solid #00437b;
                    color: #00437b;
                }
            }
        }
    }
    
    .view_right {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        >span {
            position: absolute;
            top: 35%;
            left: 50%;
            transform: translate(-50%,-50%);
            color: #fff;
            border: 1px solid #Fff;
            padding: 10px 30px;
            cursor: pointer;
            
            &:hover {
                border: 1px solid #00437b;
                color: #00437b;
            }
        }

        .view_bottom {
            img {
                cursor: pointer;

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

.video_wrap {
    margin: 80px auto;

    h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }
}
.product_sort_wrap {
    margin: 80px auto;

    .sort_btn {
        text-align: right;
        margin-bottom: 10px;
    
        h4 {
            text-align: center;
            font-weight: 800;
            font-size: 2rem;
            padding-bottom: 20px;
            color: #333;
        }
    
        button {
            margin-left: 10px;
            font-size: 14px;
            border-radius: 0;
            padding: 3px 10px;
        }
    }
}


.news_wrap {
    width: 90%;
    margin: 80px auto;

    h4 {
        text-align: center;
        font-weight: 800;
        font-size: 2rem;
        padding-bottom: 20px;
        color: #333;
    }

    h5 {
        line-height: 3;
        font-weight: 600;
        font-size: 1.1rem;
    }

    img {

        &:hover {
            opacity: 0.8;
            cursor: pointer;
        }
    }
}

footer {
    width: 100%;
    margin-top: 150px;

    .footer_inner {
        width: 70%;
        margin: 30px auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        cursor: default;

        img {
            width: 4rem;
            margin-top: 8px;
        }

        .info {
            color: #333;
            
            ul {

                &:first-child {
                    display: flex;

                    li {
                        padding-right: 10px;
                    }
                }
            }

            p {
                padding-left: 32px;
                font-size: 0.8rem;
            }

            li {
                font-size: 0.8rem;
            }
        }

        .center {

            color: #333;

            h6 {
                font-weight: 600;
                padding-left: 32px;
            }

            h1 {
                font-weight: 600;
                padding-left: 32px;
            }

            ul {
                li {
                    font-size: 0.9rem;
                }
            }
        }

        .notice {

            div {
                padding-left: 32px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                h6 {
                    font-weight: 600;
                }             
                
                button {
                    border-radius: 0;
                    font-size: 0.5rem;
                    line-height: 0;
                    padding: 8px 8px;
                }

            }

            ul {

                li {
                    font-size: 0.85rem;
                    border-bottom: 1px solid #dfdfdf;
                    padding: 5px 0;
                    cursor: pointer;
                }
            }
        }
    }
}
